const Header = props => (
  <div>
    {props.message !== null && <p>Your booking has succeeded</p>}
    <pre>{JSON.stringify(props.message)}</pre>
    <style jsx>{`
      pre {
        font-family: "Arial";
        padding: 20;
        color: #39f;
      }
    `}</style>
  </div>
);

export default Header;
